function getToken() {
  let matches = document.cookie.match(
    new RegExp(
      "(?:^|; )" +
        TokenName.replace(/([\\.$?*|{}\\(\\)\\[\]\\\\/\\+^])/g, "\\$1") +
        "=([^;]*)"
    )
  );
  return matches ? decodeURIComponent(matches[1]) : undefined;
}
function deleteToken() {
  document.cookie = TokenName + "=; Max-Age=0";
}
export function logOut() {
  deleteToken();
  return true;
}
