export async function auth() {
  const token = getToken();
  if (!token) {
    return new Promise((resolve) => {
      setTimeout(() => {
        resolve({
          data: { result: "invalid" },
        });
      }, 100);
    });
  } else {
    headers.Authorization = `Bearer ${getToken()}`;
    return axios({
      headers,
      method: "GET",
      url: `${apiUrl}/header/TokenTest`,
      mode: "no-cors",
    });
  }
}
