import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);
const routes = [
  {
    path: "/",
    name: "home",
    component: () =>
      import(/* webpackChunkName: "home" */ "../views/HomeView.vue"),
  },
  {
    path: "/tracking",
    name: "tracking",
    component: () =>
      import(/* webpackChunkName: "tracking" */ "../views/TrackingView.vue"),
  },
  {
    path: "/voyage",
    name: "voyage",
    component: () =>
      import(/* webpackChunkName: "voyage" */ "../views/VoyageView.vue"),
  },
  {
    path: "/data",
    name: "data",
    component: () =>
      import(/* webpackChunkName: "data" */ "../views/DataView.vue"),
  },
  {
    path: "/alarm",
    name: "alarm",
    component: () =>
      import(/* webpackChunkName: "alam" */ "../views/AlarmView.vue"),
  },
  {
    path: "/solution",
    name: "solution",
    component: () =>
      import(/* webpackChunkName: "solution" */ "../views/SolutionView.vue"),
  },
  {
    path: "/diagnostics",
    name: "diagnostics",
    component: () =>
      import(
        /* webpackChunkName: "diagnostics" */ "../views/DiagnosticsView.vue"
      ),
  },
  {
    path: "/information",
    name: "information",
    component: () =>
      import(
        /* webpackChunkName: "information" */ "../views/InformationView.vue"
      ),
  },
  {
    path: "/setting",
    name: "setting",
    component: () =>
      import(/* webpackChunkName: "setting" */ "../views/SettingView.vue"),
  },
  {
    path: "/master",
    name: "master",
    component: () =>
      import(/* webpackChunkName: "master" */ "../views/MasterView.vue"),
  },
  {
    path: "/custom",
    name: "custom",
    component: () =>
      import(/* webpackChunkName: "master" */ "../views/CustomView.vue"),
  },
];
const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
