<template>
  <div class="mainContainer">
    <!-- 상단 바 -->
    <v-app-bar class="elevation-0" app fluid>
      <v-app-bar-nav-icon @click="drawer = !drawer"></v-app-bar-nav-icon>
      <v-toolbar-title>AI-INS Console</v-toolbar-title>
      <v-spacer></v-spacer>
      <div class="userInfo">
        <span class="userInfoRole">{{ userRole }}</span>
        <span class="userInfoName">{{ userName }}</span>
      </div>
      <v-btn icon @click="userLogOut">
        <v-icon>mdi-logout</v-icon>
      </v-btn>
    </v-app-bar>
    <!-- // 상단 바 -->

    <!-- 메뉴 -->
    <v-navigation-drawer app v-model="drawer" class="pa-1">
      <v-list dense>
        <v-subheader>MENU</v-subheader>
        <v-list-item-group v-model="selectedItem" color="primary">
          <v-list-item
            v-for="(item, idx) in pages"
            :key="idx"
            :to="item.to"
            active-class="blue white--text"
          >
            <!-- <v-list-item-icon>
              <v-icon v-text="item.icon"></v-icon>
            </v-list-item-icon> -->
            <v-list-item-content>
              <v-list-item-title v-text="item.title"></v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>
    <!-- // 메뉴 -->

    <!-- 컨테이너 -->
    <v-main>
      <router-view />
    </v-main>
    <!-- // 컨테이너 -->
  </div>
</template>
<style>
html,
body {
  height: 100%;
  overflow: hidden;
  position: relative;
}
#app {
  height: 100%;
}
.v-main__wrap {
  height: 100%;
}
.mainContainer {
  height: 100%;
  overflow: hidden;
}
.v-main {
  height: 100%;
}
.v-toolbar__content .v-select {
  flex: 0 0 auto;
  width: 200px;
}
</style>
<script>
import { ref, computed } from "vue";
import { logOut } from "@/services/user.js";
import {
  useAuthStore,
  useMqttStore,
  useMenuStore,
} from "@/stories/mainStore.js";

export default {
  watch: {
    $route(to, from) {
      if (to.name !== "home") {
        if (this.mqttStore.mqttClient !== null) {
          this.mqttStore.disconnectClient();
        }
      }
    },
  },
  setup() {
    //
    const authStore = useAuthStore();
    const mqttStore = useMqttStore();
    const menuStore = useMenuStore();

    const selectedItem = ref(null);
    const drawer = ref(true);
    const defaultPermission = ref("master");
    const permissions = ref(["master", "visitor"]);

    const userName = ref(authStore.userInfo?.name);
    const userRole = ref(authStore.userInfo?.role);

    const pageArr = [
      {
        title: "Dashboard",
        value: "home",
        to: "/",
      },
    ];
    menuStore.menus.map((menu) => {
      pageArr.push({
        title: menu.name,
        value: menu.name,
        to: `/${menu.name}`,
      });
    });

    const pages = ref(pageArr);
    const userLogOut = async () => {
      await logOut();
      authStore.isAuth = false;
      alert("정상적으로 로그아웃 되었습니다.");
    };

    return {
      userName,
      userRole,

      mqttStore,
      userLogOut,

      selectedItem,
      drawer,
      defaultPermission,
      permissions,
      pages,
    };
  },
};
</script>
