import { defineStore } from "pinia";
import {
  ref,
  // computed
} from "vue";

export const useMenuStore = defineStore("menu", () => {
  const menus = ref(null);
  const tabs = ref(new Map());

  const setMenus = (value) => {
    menus.value = value;
    value.map((item) => {
      console.log(item.name);

      tabs.value.set(item.name, item.tabs);
    });
  };
  return { menus, tabs, setMenus };
});

export const useAuthStore = defineStore("auth", () => {
  //
  const uid = ref("");
  const errorState = ref("");
  const isAuth = ref(null);
  const userInfo = ref(null);

  function setAuth(state) {
    isAuth.value = state;
  }
  function setUserInfo(info) {
    userInfo.value = info;
  }
  return { uid, errorState, isAuth, setAuth, userInfo, setUserInfo };
});

export const useMqttStore = defineStore("mqtt", () => {
  //
  const mqttClient = ref(null);
  function setClient(client) {
    mqttClient.value = client;
  }
  function disconnectClient() {
    console.log("disconnect");
    mqttClient.value.disconnect("tv_local");
    mqttClient.value = null;
  }

  return { mqttClient, setClient, disconnectClient };
});
